<template>
  <div>
    <b-row class="mt-2">
      <b-col lg="12">
        <b-card>
          <template #header>
            <h5 class="mb-0"><b-icon icon="card-checklist" aria-hidden="true"></b-icon> PLANILLA DE HABILITACIONES FINALES</h5>
          </template>
          <b-card-text>
            <b-row>
              <b-col lg="3">
                <b-form-group label="Seleccione la Sede:" label-for="sedes" class="etiqueta">
                  <b-form-select  id="sedes" ref="sedes" v-model="idSede" :options="comboSedes" @change="idCurso=null,idMatricula=null,ocuparComboCursosSede()"></b-form-select>
                </b-form-group>
              </b-col>
              <b-col lg="3">
                <b-form-group label="Seleccione el Curso:" label-for="cursos" class="etiqueta">
                  <b-form-select  id="cursos" ref="cursos" v-model="idCurso" :options="comboCursosSede" @change="idMatricula=null,ocuparComboEstudiantes(),consultaAsignaturasCurso()" :disabled="idSede!=null ? false : true"></b-form-select>
                </b-form-group>
              </b-col>
              <b-col lg="6">
                <b-form-group label="Seleccione el Estudiante:" label-for="estud" class="etiqueta">
                  <b-form-select  id="estud" ref="estud" v-model="idMatricula" :options="comboEstudiantes" @change="estudiantesNotas()" :disabled="idCurso!=null ? false : true"></b-form-select>
                </b-form-group>
              </b-col>
            </b-row>
            <div v-if="idMatricula!=null">
              <b-row><b-col lg="12"><hr></b-col></b-row>
              <b-row>
                <b-col>
                <div v-if="btnCargando">
                  <div class="text-center m-5 text-primary">
                    <b-spinner style="width: 3rem; height: 3rem;" label="Spinner"></b-spinner>
                    <br><strong>Cargando planilla...</strong>
                  </div>
                </div>
                  <div v-else>
                    <b-row class="mt-2">
                      <b-col>
                        <vue-good-table ref="table" :columns="encabColumnas" :rows="listaNotasAsignaturasCurso" styleClass="vgt-table condensed bordered striped" :line-numbers="true">
                          <template slot="table-row" slot-scope="props">
                            <span v-if="props.column.field == 'nombreAsignatura'">
                              <span>{{props.row.nombreAsignatura}}</span>
                            </span>
                            <span v-if="props.column.field == 'promedioFinal'">
                              <span><b>{{props.row.promedioFinal}}</b></span>
                            </span>
                            <span v-if="props.column.field == 'desempeno'">
                              <span><b>{{props.row.desempeno}}</b></span>
                            </span>
                            <span v-if="props.column.field == 'habilitacion'">
                              <b-form-input v-model="props.row.habilitacion" @blur="actualizarItemNota(props.row)" @change="cambioPlanilla" autocomplete="off" maxlength="3" @keydown="handleKeyNavigation($event, props.row.originalIndex, 'habilitacion'),soloDecimales($event)" :ref="'habilitacion-' + props.row.originalIndex" :disabled="props.row.promedioFinal>0 ? false : true"></b-form-input>
                            </span>
                            <span v-if="props.column.field == 'acta'">
                              <b-form-input v-model="props.row.acta" @blur="actualizarItemActa(props.row)" @change="cambioPlanilla" autocomplete="off" maxlength="100" @keydown="handleKeyNavigation($event, props.row.originalIndex, 'acta')" :ref="'acta-' + props.row.originalIndex" :disabled="props.row.promedioFinal>0 ? false : true"></b-form-input>
                            </span>
                            <span v-if="props.column.field == 'fecha'">
                              <b-form-input type="date" v-model="props.row.fecha" @blur="actualizarItemFecha(props.row)" @change="cambioPlanilla" autocomplete="off" @keydown="handleKeyNavigation($event, props.row.originalIndex, 'fecha'),soloDecimales($event)" :ref="'fecha-' + props.row.originalIndex" :disabled="props.row.promedioFinal>0 ? false : true"></b-form-input>
                            </span>
                          </template>
                          <div slot="emptystate">
                            <h5 class="text-danger ml-5">No existen asignaturas en la planilla</h5>
                          </div>
                        </vue-good-table>
                      </b-col>
                      <b-col lg="12">
                        <b-alert v-if="cambioActivo==true" variant="danger" show><b>Advertencia: </b>La planilla se ha modificado, se recomienda guardar la planilla.</b-alert>
                      </b-col>
                      <b-col v-if="!botonGuardando">
                        <b-button class="small mx-1 mt-4" variant="success" @click="botonGuardando=true,guardarPlanilla()" :disabled="!cambioActivo">Guardar Planilla de Habilitaciones</b-button>
                      </b-col>
                      <b-col v-else>
                        <b-button class="mx-1 mt-4" variant="primary">
                          <b-spinner small type="grow"></b-spinner>
                          Guardando la planilla...
                        </b-button>
                      </b-col>
                    </b-row>
                  </div>
                </b-col>
              </b-row>
            </div>
          </b-card-text>
          <template #footer>
            <em>ActualizaciÃ³n de Habilitaciones.</em>
          </template>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
  import axios from "axios"
  import * as CONFIG from '@/assets/config.js'
  import 'vue-good-table/dist/vue-good-table.css'
  import { VueGoodTable } from 'vue-good-table'

  export default {
    name: 'habilitaciones',
    components: {
      VueGoodTable,
    },
    data () {
      return {
        idSede: null,
        comboSedes: [],
        idCurso: null,
        comboCursosSede: [],
        idMatricula: null,
        comboEstudiantes: [],
        listaAsignaturasCurso: [],
        encabColumnas: [
          { label: 'Asignatura', field: 'nombreAsignatura', sortable: false },
          { label: 'Final', field: 'promedioFinal', sortable: false },
          { label: 'Desemp.', field: 'desempeno', sortable: false },
          { label: 'Habil', field: 'habilitacion', width: '5%', sortable: false },
          { label: 'Acta Habil', field: 'acta', sortable: false },
          { label: 'Fecha', field: 'fecha', sortable: false },
        ],
        notas: [],
        btnCargando: false,
        cambioActivo: false,
        botonGuardando: false,
        listaNotasAsignaturasCurso: [],
      }
    },
    methods: {
      async guardarPlanilla() {
        this.btnCargando = true
        let habilitacionesAEnviar = []
        this.listaNotasAsignaturasCurso.forEach(element => {
          if (element.habilitacion !== null && element.fecha !== null && element.acta !== null) habilitacionesAEnviar.push(element)
        })
        //console.log(JSON.stringify(habilitacionesAEnviar))
        await axios
        .post(CONFIG.ROOT_PATH + 'academico/habilitaciones/estudiante', JSON.stringify(habilitacionesAEnviar), { headers: {"Content-Type": "application/json; charset=utf-8" }})
        .then(response => {
          if (response.data.error){
            this.mensajeEmergente('danger',CONFIG.TITULO_MSG,response.data.mensaje + ' - Actualizar Planilla Habilitaciones')
            this.btnCargando = false
          } else{
            this.mensajeEmergente('success',CONFIG.TITULO_MSG,'La planilla de Habilitaciones se ha guardado satisfactoriamente.')
          }
        })
        .catch(err => {
          this.mensajeEmergente('danger',CONFIG.TITULO_MSG,'Algo salio mal y no se pudo realizar: Actualizar Planilla Habilitaciones. Intente mÃ¡s tarde. ' + err)
          this.btnCargando = false
        })
        this.btnCargando = false
        this.botonGuardando = false
        this.cambioActivo = false
      },
      actualizarItemFecha(item) {
        let indice = this.listaNotasAsignaturasCurso.findIndex(asigna => asigna.idAsignaturaCurso === item.idAsignaturaCurso)
        if (item.fecha == '' || item.fecha == null || item.fecha == 0) {
          this.listaNotasAsignaturasCurso[indice].fecha = null
          item.fecha = null
        } else  {
          this.listaNotasAsignaturasCurso[indice].fecha = item.fecha
        }
      },
      actualizarItemActa(item) {
        //console.log(item)
        let indice = this.listaNotasAsignaturasCurso.findIndex(asigna => asigna.idAsignaturaCurso === item.idAsignaturaCurso)
        if (item.acta == '' || item.acta == null || item.acta == 0) {
          this.listaNotasAsignaturasCurso[indice].acta = null
          item.acta = null
        } else  {
          this.listaNotasAsignaturasCurso[indice].acta = item.acta
        }
      },
      actualizarItemNota(item) {
        let notaMinima = 1
        let notaMaxima = 5
        let indice = this.listaNotasAsignaturasCurso.findIndex(asigna => asigna.idAsignaturaCurso === item.idAsignaturaCurso)
        if (item.habilitacion == '' || item.habilitacion == null || item.habilitacion == 0) {
          this.listaNotasAsignaturasCurso[indice].habilitacion = null
          item.habilitacion = null
        } else  {
          if (item.habilitacion >= notaMinima && item.habilitacion <= notaMaxima) {
            this.listaNotasAsignaturasCurso[indice].habilitacion = item.habilitacion
          } else {
            this.listaNotasAsignaturasCurso[indice].habilitacion = null
            this.mensajeEmergente('danger',CONFIG.TITULO_MSG,'La habilitacion (nota): ' + item.habilitacion  + ' no es vÃ¡lida.')
            item.habilitacion = null
          }
        }
      },
      generarPromediosFinalesConDesempeno(asignaturas, notas) {
        // Agrupar por matrÃ­cula + asignatura
        const agrupado = {}
        notas.forEach(nota => {
          const key = `${nota.idMatricula}-${nota.asignatura}`
          if (!agrupado[key]) {
            agrupado[key] = {
              idMatricula: nota.idMatricula,
              asignatura: nota.asignatura,
              habilitacion: nota.habilitacion,
              fecha: nota.fecha,
              acta: nota.acta,
              vigencia: this.$store.state.aLectivo,
              valores: []
            }
          }
          const valor = parseFloat(nota.notaFinal)
          if (!isNaN(valor)) {
            agrupado[key].valores.push(valor)
          }
        })
        // Construir array final con promedio y desempeÃ±o
        const resultado = Object.values(agrupado).map(item => {
          const meta = asignaturas.find(a => a.asignatura === item.asignatura)
          // ð¡ ExclusiÃ³n directa si no existe o si orden === 99
          if (!meta || meta.orden === 99) return null
          const suma = item.valores.reduce((a, b) => a + b, 0)
          const promedio = item.valores.length > 0 ? this.redondear(suma / item.valores.length).toFixed(1) : null
          const tipo = meta.idTipoEspecialidad
          let desempeno = ''
          if (promedio !== null) {
            if (tipo == 1)
              if (promedio < this.datosSeccion.minBas) desempeno = 'BAJO'
              else if (promedio < this.datosSeccion.minAlt) desempeno = 'BÃSICO'
              else if (promedio < this.datosSeccion.minSup) desempeno = 'ALTO'
              else desempeno = 'SUPERIOR'
            else {
              if (promedio < this.datosSeccion.minBasT) desempeno = 'BAJO'
              else if (promedio < this.datosSeccion.minAltT) desempeno = 'BÃSICO'
              else if (promedio < this.datosSeccion.minSupT) desempeno = 'ALTO'
              else desempeno = 'SUPERIOR'
            }
          }
          return {
            idMatricula: item.idMatricula,
            idAsignaturaCurso: meta?.idAsignaturaCurso || null,
            nombreAsignatura: meta?.nombreAsignatura || item.asignatura,
            promedioFinal: promedio ? promedio : '',
            desempeno,
            habilitacion: item.habilitacion,
            fecha: item.fecha,
            acta: item.acta,
            vigencia: item.vigencia,
            orden: meta.orden
          }
        }).filter(e => e !== null) // â Filtra los excluidos
        // Ordenar por el campo "orden"
        return resultado.sort((a, b) => a.orden - b.orden)
      },
      async estudiantesNotas() {
        this.btnCargando = true
        this.notas = []
        await axios
        .get(CONFIG.ROOT_PATH + 'academico/notashabilitaciones/estudiante', {params: {idCurso: this.idCurso, idMatricula: this.idMatricula, vigencia: this.$store.state.aLectivo}})
        .then(response => {
          if (response.data.error){
            this.mensajeEmergente('danger',CONFIG.TITULO_MSG,response.data.mensaje + ' - Consolidados asignaturas curso periodo')
            this.btnCargando = false
          } else{
            if (response.data.datos != 0) {
              this.notas = response.data.datos
            }
          }
        })
        .catch(err => {
          this.mensajeEmergente('danger',CONFIG.TITULO_MSG,'Algo salio mal y no se pudo realizar: Consolidados asignaturas curso periodo. Intente mÃ¡s tarde.' + err)
          this.btnCargando = false
        })
        //console.log(JSON.stringify(this.notas))
        this.listaNotasAsignaturasCurso = this.notas.length ? this.generarPromediosFinalesConDesempeno(this.listaAsignaturasCurso, this.notas) : false
        this.btnCargando = false
      },
      async consultaAsignaturasCurso() {
        this.listaAsignaturasCurso = []
        await axios
        .get(CONFIG.ROOT_PATH + 'academico/asignacioncurso/habilitaciones', {params: {idCurso: this.idCurso}})
        .then(response => {
          if (response.data.error){
            this.mensajeEmergente('danger',CONFIG.TITULO_MSG,response.data.mensaje + ' - Lista asignaturas curso')
          } else{
            if (response.data.datos != 0) {
              this.listaAsignaturasCurso = response.data.datos
            }
          }
        })
        .catch(err => {
          this.mensajeEmergente('danger',CONFIG.TITULO_MSG,'Algo salio mal y no se pudo realizar: Lista asignaciÃ³n docentes curso. Intente mÃ¡s tarde.' + err)
        })
        //console.log(JSON.stringify(this.listaAsignaturasCurso))
      },
      async ocuparComboEstudiantes() {
        this.comboEstudiantes = []
        await axios
        .get(CONFIG.ROOT_PATH + 'academico/listacurso/habilitaciones', { params: { idCurso: this.idCurso, vigencia: this.$store.state.aLectivo }})
        .then(response => {
          if (response.data.error){
            this.mensajeEmergente('danger',CONFIG.TITULO_MSG,response.data.mensaje + ' - Consulta Lista Curso')
          } else{
            if (response.data.datos != 0) {
              response.data.datos.forEach(element => {
                this.comboEstudiantes.push({ 'value': element.idMatricula, 'text': element.estudiante })
              })
            }
          }
        })
        .catch(err => {
          this.mensajeEmergente('danger',CONFIG.TITULO_MSG,'Algo salio mal y no se pudo realizar: Consulta Lista Curso. Intente mÃ¡s tarde.' + err)
        })
      },
      async ocuparComboCursosSede() {
        this.comboCursosSede = []
        this.$store.state.datosCursos.forEach(element => {
          if (element.id_sede == this.idSede) {
            this.comboCursosSede.push({ 'value': element.id, 'text': element.nomenclatura.toUpperCase() })
          }
        })
      },
      async ocuparComboSedes() {
        this.comboSedes = []
        this.$store.state.datosSedes.forEach(element => {
          this.comboSedes.push({ 'value': element.id, 'text': element.sede.toUpperCase() })
        })
      },
      handleKeyNavigation(event, i, col) {
        if (event.key === "ArrowDown") {
          const nextInput = this.$refs[`${col}-${i + 1}`]
          if (nextInput) nextInput.focus()
        } else if (event.key === "ArrowUp") {
          const previousInput = this.$refs[`${col}-${i - 1}`]
          if (previousInput) previousInput.focus()
        }
      },
      redondear(num) {
        var m = Number((Math.abs(num) * 10).toPrecision(15))
        return Math.round(m) / 10 * Math.sign(num);
      },
      soloNumeros(e) {
        let key = window.Event ? e.which : e.keyCode
        if (!((key >= 48 && key <= 57) || (key >= 96 && key <= 105) || (key == 8) || (key == 9) || (key == 37) || (key == 39))) {
            e.preventDefault()
        }
      },
      soloDecimales(e) {
        let key = window.Event ? e.which : e.keyCode
        if (!((key >= 48 && key <= 57) || (key >= 96 && key <= 105) || (key == 8) || (key == 9) || (key == 37) || (key == 39) || (key == 110) || (key == 189) || (key == 190))) {
            e.preventDefault()
        }
      },
      tdClassFuncDiversa(fila) {
        if (fila.id_diversa == 'S') {
          return 'text-center alert alert-warning'
        }
      },
      cambioPlanilla() {
        this.cambioActivo = true
      },
      mensajeEmergente(variante, titulo, contenido) {
        this.$bvToast.toast(contenido, { title: titulo, variant: variante, toaster: "b-toaster-top-center", solid: true, autoHideDelay: 4000, appendToast: false })
      }
    },
    beforeMount() {
      if(this.$store.state.idRol == 1 || this.$store.state.idRol == 12 || this.$store.state.perActNotas == 1) {
        this.ocuparComboSedes()
        this.datosSeccion = this.$store.state.datosSecciones[this.$store.state.idSeccion - 1]
        //console.log(JSON.stringify(this.datosSeccion))
      } else {
        this.$router.push('/restringida')
      }
    },
    computed: {
    }
  }
</script>